.navbar {
	position: -webkit-sticky;
	position: sticky;
	z-index: 3;
	top: 0;
}

.wrapper {
	position: relative;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	background: #fff;
	/* justify-content: space-around; */
}

.logo {
	/* background: url('images/SIS-LOGO-120x80.png') no-repeat; */
	height: 80px;
	/* width: 100%; */
	-webkit-flex: 2 1;
	        flex: 2 1;
	/* z-index: 1; */
}
.navbar-list {
	text-decoration: none;
	list-style: none;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	margin: 0;
	padding: 0;
	-webkit-flex: 5 1;
	        flex: 5 1;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
}

.nav-item > a::after {
	content: "";
	display: block;
	width: 0;
	height: 2px;
	background: #d11717;
	transition: width 0.3s;
}
.nav-item > a:hover::after {
	width: 100%;
	transition: width 0.3s;
}

.nav-item {
	/* margin: 0 10px; */
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}
.nav-item > a {
	font-size: 14px;
	font-weight: bold;
	color: #100f0f;
	text-decoration: none;
}

.menu-icon {
	display: none;
}

.bars,
.times {
	font-size: 2rem;
	color: #18ba60;
}

