@media (max-width: 770px) {
	.navbar-list {
		-webkit-flex-direction: column;
		        flex-direction: column;
		width: 100%;
		position: absolute;
		top: 80px;
		-webkit-align-items: start;
		        align-items: start;
		-webkit-flex: 0 1;
		        flex: 0 1;
		margin: 0;
		height: 300px;
		left: -100%;
		opacity: 1;
		transition: all 0.5s ease;
		padding-left: 2em;
	}

	.active {
		left: 0;
		opacity: 1;
		transition: all 0.5s ease;
		z-index: 1;
		background: #222;
	}
	.nav-item a {
		color: #fff;
	}
	.logo {
		position: relative;
		top: 0;
		left: 0;
		-webkit-transform: translate(25%, 0%);
		        transform: translate(25%, 0%);
		-webkit-flex: 0 1;
		        flex: 0 1;
	}
	.menu-icon {
		display: block;
		position: absolute;
		top: 0;
		right: 0;
		-webkit-transform: translate(-100%, 60%);
		        transform: translate(-100%, 60%);
	}
}

